/* Navigation Bar */
.navbar {
  background-color: #fbfbfb;
  position: fixed;
  top: 50px;
  left: 0;
  right: 0;
  width: 85%;
  height: 100px;
  margin: auto;
  box-shadow: 0 0 5px 0 rgba(black, .5);
  padding: 0 30px;
  z-index: 9999;
  transition: .2s;
  @media #{$mediaExSmall} {
    height: auto;
    padding: 20px;
  }
  @media #{$mediaSmall} {
    height: auto;
    padding: 20px;
  }
  @media #{$mediaMedium} {
    height: auto;
    padding: 20px;
  }
  @media #{$mediaLarge} {
    
  }
  .navbar-brand {
    @media #{$mediaExSmall} {
      margin-bottom: 20px;
    }
  }
  .navbar-collapse {
    .navbar-nav {
      @media #{$mediaExSmall} {
        padding-top: 40px;
      }
      @media #{$mediaSmall} {
        padding-top: 40px;
      }
      @media #{$mediaMedium} {
        padding-top: 40px;
      }
      @media #{$mediaLarge} {
        
      }
      .nav-item.active {
        border-top: 4px solid $brandColor !important;
        color: #5d6e7b;
        @media #{$mediaExSmall} {
          border-top: none !important
        }
        @media #{$mediaSmall} {
          border-top: none !important
        }
        @media #{$mediaMedium} {
          border-top: none !important
        }
        @media #{$mediaLarge} {
          
        }
      }
      .nav-item {
        border-top: 4px solid transparent;
        height: 100px;
        line-height: 100px;
        width: 100px;
        text-align: center;
        transition: .3s;
        @media #{$mediaExSmall} {
          height: auto;
          line-height: 60px;
          border-top: none !important
        }
        @media #{$mediaSmall} {
          height: auto;
          line-height: 60px;
          border-top: none !important
        }
        @media #{$mediaMedium} {
          height: auto;
          line-height: 60px;
          border-top: none !important
        }
        @media #{$mediaLarge} {
          
        }
        .nav-link {
          padding: 0;
          font-size: 14px;
          font-weight: 500;
          color: #5d6e7b;
          transition: .2s;
          i { padding: 0 5px }
        }
        &:hover {
          .nav-link {
            color: rgba(#5d6e7b, .8)
          }
          border-top: 4px solid $brandColor;
        }
      }
    }
  }
}