/* Section About Us */
.about-us {
  border-top: 100px solid $mainColor;
  padding: 100px 0 0;
  text-align: center;
  background-color: #e5eaed;
  h1 {
    font-size: 30px;
    line-height: 36px;
    padding-bottom: 25px;
    margin-bottom: 20px;
    color: $mainColor;
    position: relative;
    @media #{$mediaExSmall} {
      font-size: 20px;
      line-height: 26px;
      padding-bottom: 15px
    }
    @media #{$mediaSmall} {
      font-size: 23px;
      line-height: 29px;
      padding-bottom: 17px
    }
    @media #{$mediaMedium} {
      font-size: 26px;
      line-height: 33px;
      padding-bottom: 20px
    }
    @media #{$mediaLarge} {
      font-size: 28px;
      line-height: 34px;
      padding-bottom: 22px
    }
    &::after {
      content: "";
      position: absolute;
      bottom: 0;
      left: 50%;
      transform: translate(-50%, 0);
      width: 50px;
      height: 3px;
      background-color: $brandColor;
    }
  }
  p {
    font-size: 16px;
    color: $mainColor;
    padding-bottom: 50px;
    @media #{$mediaExSmall} {
      font-size: 14px;
    }
    @media #{$mediaSmall} {
      font-size: 14px;
    }
    @media #{$mediaMedium} {
      font-size: 15px;
    }
    @media #{$mediaLarge} {
      font-size: 15px;
    }
  }
}

/* Section Modern Cars */
.modern-cars {
  padding: 60px 0;
  text-align: center;
  h2 {
    font-size: 28px;
    margin-bottom: 15px;
    color: $mainColor;
    @media #{$mediaExSmall} {
      font-size: 20px;
    }
    @media #{$mediaSmall} {
      font-size: 23px;
    }
    @media #{$mediaMedium} {
      font-size: 26px;
    }
    @media #{$mediaLarge} {
      font-size: 28px;
    }
  }
  p {
    font-size: 15px;
    color: $mainColor;
    padding-bottom: 15px;
    margin-bottom: 50px;
    position: relative;
    @media #{$mediaExSmall} {
      font-size: 14px;
    }
    @media #{$mediaSmall} {
      font-size: 14px;
    }
    @media #{$mediaMedium} {
      font-size: 14px;
    }
    @media #{$mediaLarge} {
      font-size: 15px;
    }
    &::after {
      content: "";
      position: absolute;
      bottom: 0;
      left: 50%;
      transform: translate(-50%, 0);
      width: 50px;
      height: 3px;
      background-color: $brandColor;
    }
  }
  .item {
    margin-bottom: 40px;
    text-align: right;
    box-shadow: 0 0 3px 0 rgba(black, .2);
    border-radius: 3px;
    overflow: hidden;
    font-family: Arial, Helvetica, sans-serif;
    img {
      height: 250px;
      transition: .3s;
      &:hover {
        opacity: .8;
      }
    }
    span {
      display: block;
      padding: 10px;
    }
  }
  .more {
    display: block;
    width: 100px;
    height: 45px;
    line-height: 40px;
    margin: 0 auto;
    text-decoration: none;
    border: 3px solid transparent;
    color: #fff;
    background-color: $brandColor;
    transition: .3s;
    padding: 0;
    &:hover {
      opacity: .8;
    }
  }
}

/* Section Reservation */
.reservation {
  background-color: $brandColor;
  padding: 0;
  .img {
    background-image: url("../images/cta.jpg");
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    height: 100%;
    width: 100%;
    position: relative;
  }
  .text {
    text-align: right;
    padding: 30px 0 0;
    position: relative;
    @media #{$mediaExSmall} {
      padding: 30px;
    }
    @media #{$mediaSmall} {
      padding: 30px;
    }
    @media #{$mediaMedium} {
      padding: 30px;
    }
    @media #{$mediaLarge} {
    }
    h3 {
      font-size: 30px;
      font-weight: normal;
      color: #fff;
      @media #{$mediaExSmall} {
        font-size: 20px;
      }
      @media #{$mediaSmall} {
        font-size: 20px;
      }
      @media #{$mediaMedium} {
        font-size: 23px;
      }
      @media #{$mediaLarge} {
      }
    }
    p {
      font-size: 16px;
      color: #fff;
      @media #{$mediaExSmall} {
        font-size: 14px;
      }
      @media #{$mediaSmall} {
        font-size: 14px;
      }
      @media #{$mediaMedium} {
        font-size: 15px;
      }
      @media #{$mediaLarge} {
        font-size: 15px;
      }
    }
    &::after {
      content: "";
      position: absolute;
      top: 0;
      right: -70px;
      width: 60px;
      height: 100%;
      border-bottom: 114px solid $brandColor;
      border-right: 35px solid transparent;
      @media #{$mediaExSmall} {
        display: none
      }
      @media #{$mediaSmall} {
        display: none
      }
      @media #{$mediaMedium} {
        display: none
      }
      @media #{$mediaLarge} {
        border-bottom: 112px solid $brandColor;
      }
    } 
  }
  .now {
    width: 100px;
    height: 50px;
    line-height: 45px;
    margin: auto;
    margin-left: 40px;
    margin-top: 40px;
    display: block;
    text-align: center;
    color: #fff;
    border: 4px solid #fff;
    text-decoration: none;
    transition: .2s;
    &:hover {
      background-color: #fff;
      color: $brandColor
    }
    @media #{$mediaExSmall} {
      margin-top: 0px;
      margin-bottom: 20px
    }
    @media #{$mediaSmall} {
      margin-top: 0px;
      margin-bottom: 20px
    }
    @media #{$mediaMedium} {
      
    }
    @media #{$mediaLarge} {
    }
  }
}

